import React from "react"
import "./Header.css"
import logo from "./../../images/logo2.jpg"

export default function Header() {
    return (
        <>
            <header className="center">
                {/* EXIBIO PARA WEB */}
                <div className="div-esquerda no-mobile">
                    <div>
                        <p>&nbsp;&nbsp;Simplificando seu trabalho.<br />&nbsp;&nbsp;Aqui voc encontrar contedos acerca das principais ferramentas digitais.</p>
                    </div>
                </div>
                <div className="div-direita no-mobile">
                    <img src={logo} alt="Logo do site" />
                </div>

                {/* EXIBIO PARA MOBILE */}
                <div className="center mobile">
                    <div className="div-imagem center mobile">
                        <img src={logo} alt="Logo do site" />

                        <div className="div-central center mobile">                          
                            <p>Simplificando seu trabalho.<br />Disponibilizando contedos acerca das principais ferramentas digitais.</p>                           
                        </div>
                    </div>
                </div>

            </header>
        </>
    )
}